import React from 'react';
import img from "../assets/img/me.jpg"
import "../assets/styles/cv.css"
import CheckIcon from '@mui/icons-material/Check';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import WhatsAppIcon from '@mui/icons-material/WhatsApp';
import FacebookIcon from '@mui/icons-material/Facebook';
import HomeIcon from '@mui/icons-material/Home';
import EmailIcon from '@mui/icons-material/Email';
import StayCurrentPortraitIcon from '@mui/icons-material/StayCurrentPortrait';
import ArrowCircleDownIcon from '@mui/icons-material/ArrowCircleDown';
import html2canvas from 'html2canvas';
import jsPDF from 'jspdf';

const MyCv =()=>{
    const data = {
        skills:[{
            name:"Front_End",
            content:[{
                name:"HTMl",
                rate:5
            },{
                name:"CSS",
                rate:4
            },{
                name:"JS",
                rate:4
            },{
                name:"React",
                rate:5
            },{
                name:"Redux",
                rate:4
            },{
                name:"MUI",
                rate:5
            }]
        },{
            name:"Back_End",
            content:[{
                name:"Node_JS",
                rate:4
            },{
                name:"MongoDB",
                rate:4
            }] 
        }],
        contact:[{
            icon:<HomeIcon style={{color:"white",fontSize:"10px",fontWeight:"800"}} />,
            value:"Syria"
        },{
            icon:<StayCurrentPortraitIcon style={{color:"white",fontSize:"10px",fontWeight:"800"}} />,
            value:"+963997944207"
        },{
            icon:<EmailIcon style={{color:"white",fontSize:"10px",fontWeight:"800"}} />,
            value:"eng.mahaab96@gmail.com"
        },{
            icon:<LinkedInIcon style={{color:"white",fontSize:"10px",fontWeight:"800"}} />,
            value:"Maha Abo Assi"
        },{
            icon:<FacebookIcon style={{color:"white",fontSize:"10px",fontWeight:"800"}} />,
            value:"/Maha Abo Assi"
        },{
            icon:<WhatsAppIcon style={{color:"white",fontSize:"10px",fontWeight:"800"}} />,
            value:"+963997944207"
        },],
        experiances:[{
            dateStart:"2021",
            dateEnd:"Present",
            experiance:"Syrian Wireless Organization(SWO)",
            des:"Governmental job, Syria",
            content:"Site engineer as a communications engineer supervises the stations and their operation"

        },{
            dateStart:"2019",
            dateEnd:"2021",
            experiance:"High School of Computer Technologie",
            des:"Governmental job, Syria",
            content:"Mathematics teacher."

        },{
            dateStart:"2021",
            dateEnd:"2023",
            experiance:"XCore",
            des:"Private Work, Syria",
            content:"Front end designer team leader"

        },{
            dateStart:"2021",
            dateEnd:"2023",
            experiance:"XCore",
            des:"Private Work, Syria",
            content:"Trainer in the front-ends design workshops for websites"

        }],
        education:[{
            dateStart:"2014",
            dateEnd:"2019",
            edu:"Communication and Electronic Engineer",
            des:"Damascus University, Damascus",
            content:"Bachelor's degree"

        },{
            dateStart:"2020",
            dateEnd:"2020",
            edu:"CCNA",
            des:"Course, Syria",
            content:`The CCNA networking course covers network
            fundamentals install, operate, configure, and
            verify basic IPv4 and IPv6 networks.
            It lasted for three months`
        },{
            dateStart:"2020",
            dateEnd:"2020",
            edu:"Practical Networks",
            des:"Course, Syria",
            content:`It lasted for three months, including network
            components, how to connect them, explaining
            the most important communication protocols,
            and a lot of things.`
        }]

    }
    const downloadPDF = () =>{
        const capture = document.querySelector('.content-bio');
        html2canvas(capture,{
            // quality:4,
            scale:5
        }).then((canvas)=>{
          const imgData = canvas.toDataURL('img/png');
          const doc = new jsPDF('p', 'mm', 'a4');
          const componentWidth = doc.internal.pageSize.getWidth();
          const componentHeight = doc.internal.pageSize.getHeight();
          doc.addImage(imgData, 'PNG', 0, 0, componentWidth, componentHeight);
          doc.save('MahaAboAssi_CV.pdf');
        })
      }
    
    return(
      
    <div className='bio-page'>
        <div className='download-icon'>
            <ArrowCircleDownIcon onClick={downloadPDF} style={{fontSize:"60px"}} className='icon'/>
        </div>
        <div className='d-flex content-bio'>
        <div className='left-side'>
            <div className='border-pink'></div>
            <div className='img-profile'>
                 <div className='border-img'>
                    <div className='son-border-img'>
                    <img src={img} />
                    </div>
                </div>
                
                
            </div>
            <div className=''>
                    <h6 className='addr-sec'>SKILLs</h6>
                    {
                        data.skills.map((e)=>{
                            return(<div className='px-3 pb-1'>
                                <div className='mb-2'  style={{fontSize:"10px",color:"silver"}} >{e.name}</div>
                                <div className='text-white' style={{fontSize:"10px"}}>{
                                    e.content.map((el)=>{
                                        let arrayStar=[]
                                        let arrayEmpty=[]
                                        let emptyNum = 5-el.rate
                                        for(let i =0 ;i<el.rate;i++){
                                            arrayStar.push(i)
                                        }
                                        for(let j =0 ;j<emptyNum;j++){
                                            arrayEmpty.push(j)
                                        }
                                        
                                        return(<div className='d-flex'>
                                        <div  style={{width:"100px"}}>{el.name}</div>
                                        <div className=' d-flex'>
                                            {arrayStar.map((em)=>{
                                                return<div className='circle-bio'></div>
                                            })}
                                            {arrayEmpty.map((em)=>{
                                                return <div className='circle-bio-outline '></div>
                                                })}
                                        
                                            
    
                                            
                                        </div>
                                    </div>
                                    )})
                        }</div>
                            </div>)
                        })
                    }
                </div>
                {/* <h6 className='addr-sec'>AVAILABLE</h6>
                    <div className='px-3 pb-1 text-white' style={{fontSize:"11px"}}>
                        <span><CheckIcon  style={{color:"rgb(168, 0, 104)",fontSize:"15px",fontWeight:"800"}} /></span> part time
                    </div> */}
                <div className='contact-bio'>
                    
                    <h6 className='addr-sec'>CONTACT</h6>
                    <div className='px-3 pb-1 text-white' style={{fontSize:"11px"}}>{
                        data.contact.map((e)=>{return(<>
                        <div className='d-flex'><span >{e.icon}</span> {e.value}</div>
                        </>)})
                    }
                        
                </div>
  
                </div>
                      </div>
        <div className='right-side'>
            <div className='section_1' >
                <h4>MAHA ABO ASSI</h4>
                <p>SENIOR FRONTEND WEB DEVELOPER</p>
            </div>
            <div className='section_2'>
                <h6>PROFILE</h6>
                <p className='pr-1'>
                My name is Maha Abu Assi. I graduated from Communications and Electronics Engineering, Damascus University,
                Communications and electronics engineer, graduate of Damascus University, with an average of 77.43%. I have more than two years of experience in designing front-end websites using <span>React</span> , in addition to designing back-end websites using <span>Node-js</span> and  <span>MongoDB</span> to connect to the database. I have several certificates in the field of communications, CCNA and Practical Network, which define the components of the network and how to communicate between them.
                </p>
            </div>
            <div className='section_2'>
             <h6>EXPERIANCE</h6>
                {data.experiances.length>0 && data.experiances.map((e)=>{
                    return(<div className='border-ele pb-2'>
                    <div className='cicular'></div>
                    <div className='d-flex'>
                        <div className='px-2 date title-content'>{e.dateStart}&nbsp;-&nbsp; {e.dateEnd}</div>
                        <div className='content'>
                            <div className='title-content'>{e.experiance}</div>
                            <div className='des'>{e.des}</div>
                            <div className='cont'>{e.content}</div>
                        </div>
                    </div>

                </div>)
                })
            }
               
                
               
            </div>
            <div className='section_2 mt-3'>
             <h6>EDUCATION</h6>
                {data.education.length>0 && data.education.map((e)=>{
                    return(<div className='border-ele pb-2'>
                    <div className='cicular'></div>
                    <div className='d-flex'>
                        <div className='px-2 date title-content'>{e.dateStart}&nbsp;-&nbsp; {e.dateEnd}</div>
                        <div className='content'>
                            <div className='title-content'>{e.edu}</div>
                            <div className='des'>{e.des}</div>
                            <div className='cont'>{e.content}</div>
                        </div>
                    </div>

                </div>)
                })
            }
               
                
               
            </div>
        </div>
    </div>
    </div>
    )

}

export default MyCv;