import React from 'react';
import {connect} from "react-redux"

const BasicCard = ({title,content,lang})=>{
    return(<div className='col-sm-12 col-md-6 col-lg-6'>
    <h4 className='my-3'>{title}</h4>
    <div >
        {content.map((e)=>{
            return(<div className='resume-card  '>
                <div className={lang==="EN"?'d-flex absolute_title':'d-flex absolute_title_Ar'}>
                    <div className={lang ==="EN"?'circular':"circular-Ar"} ></div>
                    <h4>{e.subTitle}</h4>
                </div>
                <div className={lang==="EN"?'border-card ':'border-card-Ar '}>
                    <span>{e.date}</span>
                    {e.type.length>0 && <p>{e.type} ,{e.place}</p>}
                    
                    <p>{e.description}</p>
                    <ul>{e.list.map((e)=>{return(<li>{e}</li>)})
                    }</ul>
                </div>
            </div>)
        })}
    </div>
  
</div>)

} 
function ResumePage({data,lang}) {
    return ( <div className='content p-4 '>
        <h3>{data.ResumePage.basicTitle}</h3>
        <div className='row mx-0'>
            
            {
                data.ResumePage.content.map((e)=>{
                    return(<BasicCard lang={lang} title={e.title} content={e.content} />)
                })
            }


        </div>

    </div> );
}
const mapStateToProps =(state)=>{
    return{
        lang:state.lang
    }
}

export default connect(mapStateToProps)(ResumePage);