const initStale = {
    lang :"EN",
    theme :"dark"
}

const rootReduce=(state = initStale ,action)=>{
    switch(action.type){
        case "CHANGE_LANG_TO_AR":
            return{
                ...state,
                lang:"AR"
            }
            break;
        case "CHANGE_LANG_TO_EN": 
            return{
                ...state,
                lang:"EN"
            }
            break;
        case "CHANGE_TO_DARK_THEME":
            return{
                ...state,
                theme:"dark"
            }
            break;
        case "CHANGE_TO_LIGHT_THEME":
            return{
                ...state,
                theme:"light"
            }
            break;
        default:
            return {...state}
            break;


    }

    

}
export default  rootReduce;