import React from 'react';
import img from "../assets/img/me.png"
import ColorTabs from '../component/Tabs';
import AboutContent from '../contents/About';
import ContentPagesTemplate from '../contents/ContentPagesTemplate';
import ContactPage from '../contents/Contact';
import SocialProfiles from '../component/Social';
import ResumePage from '../contents/Resume';
import TostifyHot from '../component/TostifyHot';
import  PlaceHolder  from '../component/PlaceHolder';
import dataAr from "../data/ar_data"
import dataEn from "../data/data"
import { connect } from 'react-redux';

/* CSS STYLE */
import "../assets/styles/style1.css";
// import "../assets/styles/lightStyle.css"
import "../assets/styles/breakpoint.css"
import BasicAccordion from '../component/Accordion';



function HomePage({lang,changeLangToAr,changeLangToEn}) {
    const tempChange= React.useRef(0)
    const [valueSelected,setValueSelected]= React.useState("one")
    const [isSelected,setIsSelected] = React.useState(false)
    const [data,setData] = React.useState(dataEn)
    const [dir,setDir] = React.useState("ltr")
    const [isChange,setIsChange] = React.useState(false)
    React.useEffect(()=>{
        setTimeout(()=>{
            setIsChange(false)
        },1000)
        setIsChange(true)
        if(lang === "EN"){
            setData(dataEn)
            setDir("ltr")
        }else if (lang=== "AR"){
            setData(dataAr)
            setDir("rtl")
        }
    },[lang])
    // React.useEffect(()=>{
    //      setIsSelected(true)
    //     // setTimeout(()=>{setIsSelected(false)},1000)
    // },[valueSelected])
    return (<>{isChange ?<PlaceHolder dir={dir}/>: <div style={{direction:dir}}>
        <TostifyHot isSelected={isSelected} />
        <div id='home-page'>
        
            <div className='w-100 h-100 row mx-0 ' >
                <div className='col-xl-8 col-lg-12 col-md-12 col-sm-12 d-flex justify-content-center align-items-center '>
                    <div className=' w-100 position-relative  d-flex justify-content-center '>
                        <div className='w-75 '>
                            <h1 className='name_title'>
                                {data.firstInfo.name}
                            </h1>
                            <h4 className='spc_title'>
                                
                                {data.firstInfo.spec}
                            </h4>
                            <div className='accordion-div'>
                                <BasicAccordion returnedValue={(res)=>{
                                    setValueSelected(res)
                                    setIsSelected(true)
                                  
                                }}/>
                            </div>
                            <div className='tab-div' style={{color:"white!important"}}>
                            
                                {valueSelected==="one"&&<ColorTabs changeValue="one"  returnedSelected={(res)=>{
                                        setValueSelected(res)
                                        setIsSelected(true)
                                        tempChange.current=1
                                        
                                }} />}
                            </div>
                            <div className='social_div' >
                            <SocialProfiles/>
                            </div>
                        </div>
                    </div>
                 
                </div>
                <div className='col-xl-4 col-lg-12  col-md-12 col-sm-12 d-flex justify-content-center align-items-center icon_photo '>
                    <img  src={img} alt="profile" />
                  
                </div>
            </div>
        </div>
        <div className='position-fixed d-flex justify-content-end px-3 fixed-div' style={{right:"0",left:"0",bottom:"10px",background:"black"}}>
            <div className='' >{lang === "EN"?<div>created by eng Maha Abo Assi</div>:<div> التصميم من قبل المهندسة مها أبوعاصي</div> }
                
                <div className='d-flex justify-content-center'> 
                    <span style={{cursor:"pointer"}} onClick={changeLangToEn}>{lang==="EN"?<span >English</span>:<span>انكليزي</span>}</span> &nbsp;/&nbsp; 
                    <span style={{cursor:"pointer"}} onClick={changeLangToAr}>{lang==="EN"?<span>Arabic</span>:<span>عربي</span>}</span> 
                </div>
            </div>
            
        </div>
        <ContentPagesTemplate isSelected={isSelected}   id="about-page" tempChange={tempChange.current} changeValue={valueSelected} returnedValueSelect={(res)=>{
            if(res==="one"){
                tempChange.current=3
            }
            setValueSelected(res)
        }}  
            children={valueSelected==="two"?<AboutContent data={data}/>:(valueSelected==="three"?"three":(valueSelected==="four"?<ResumePage data={data}/>:(valueSelected==="five"?<ContactPage data={data}/>:"")))} />
    
        </div>}</>
         );
}
const mapStateToProps =(state)=>{
    return{
        lang :state.lang
    }
}
const mapDispatchToProps = (dispatch)=>{
    return{
        changeLangToAr : ()=>{dispatch({type:"CHANGE_LANG_TO_AR"})},
        changeLangToEn:()=>{dispatch({type:"CHANGE_LANG_TO_EN"})}
    }

}

export default connect(mapStateToProps,mapDispatchToProps)(HomePage) ;
