import * as React from 'react';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import Typography from '@mui/material/Typography';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import {connect} from "react-redux"
import {Link} from "react-router-dom"

function BasicAccordion({lang,returnedValue}) {
    const [isEn,setIsEn] = React.useState(true)
    React.useEffect(()=>{
        if(lang=== "EN"){
          setIsEn(true)
          
        }else if(lang === "AR"){
          setIsEn(false)
        }
      },[lang])
  return (
    <div>
      <Accordion style={{background:"transparent",color:"grey"}}>
        <AccordionSummary
            style={isEn?{width:"20px",marginLeft:"50%"  ,color:"rgb(156 39 176)"}:{width:"20px",marginRight:"50%"  ,color:"rgb(156 39 176)"}}
          expandIcon={<ExpandMoreIcon style={{color:"rgb(156 39 176)"}}/>}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
           <span className='px-2' onClick={()=>{returnedValue("one")}}>    {isEn?"Home":"الصفحة الرئيسية"}</span>
        </AccordionSummary>
        <AccordionDetails>
            <ul style={{listStyle:"none",textAlign:"center"}}>
                
                <li  onClick={()=>{returnedValue("two")}}>{isEn?"About":"حول"}</li>
                <li className='my-1' onClick={()=>{returnedValue("four")}}>{isEn?"Resume":"سيرة ذاتية"}</li>
                <li onClick={()=>{returnedValue("five")}}>{isEn?"Contact":"معلومات الاتصال"} </li>
                <Link to={"/download/cv"}>
                <li className='my-1' onClick={()=>{returnedValue("six")}}>{isEn?"download":"تنزيل"}</li>
        </Link>
                
            </ul>
        </AccordionDetails>
      </Accordion>
      
    </div>
  );
}
const mapStateToProps=(state)=>{
    return{
        lang:state.lang
      }
}
export default connect(mapStateToProps)(BasicAccordion)
