import React from 'react';
import StarIcon from '@mui/icons-material/Star';
import StarOutlineIcon from '@mui/icons-material/StarOutline';
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';

function AboutContent({data}) {
    React.useEffect(()=>{
        
    })
    
    return (
    
    <div className='content'>
            <div className='py-4 sub-content row mx-0 text-white w-100'>
                {/* about page */}
                <div className='col-sm-12 col-md-12 col-lg-4 d-flex justify-content-center align-items-center'>
                    <img className=' profile-img' src={data.Aboutcontent.img} alt="" />
                </div>
                <div className=' col-sm-12 col-md-12 col-lg-8'>
                    <h3 className='profile-title' > {data.Aboutcontent.spec}</h3>
                    <p>
                        {data.Aboutcontent.summary}
                    </p>
                    <div className='row mx-0'>
                        {data.Aboutcontent.info.length >0 && data.Aboutcontent.info.map((e)=>{
                            return<div key={e.name+"123"} className='col-sm-12 col-md-6 col-lg-6'>
                            <ul style={{listStyle:"none"}}>
                                <li> <ArrowForwardIosIcon style={{color:"#d47d8a"}}/><span className='li-title px-1'>{e.name}</span> : {e.value} </li>
                            </ul>
                    </div>

                        })}
                    </div>
                </div>
            </div>
            <div className='sub-card my-4'>
                <h4 className='title px-4'>{data.Aboutcontent.skills.title}</h4>
                <div className='row mx-0 '>
                    {data.Aboutcontent.skills.content.map((e)=>{
                        //// process to stars
                        let arrayStar=[]
                        let arrayEmpty=[]
                        let emptyNum = 10-e.rating
                        for(let i =0 ;i<e.rating;i++){
                            arrayStar.push(i)
                        }
                        for(let j =0 ;j<emptyNum;j++){
                            arrayEmpty.push(j)
                        }
                        return<div className='col-sm-12 col-md-6 col-lg-6 my-3 px-4'>
                        <div className='sub-title'>{e.name}</div>
                        {arrayStar.map((e)=>{
                            return<StarIcon key={e+"123"} style={{color:"#d47d8a"}}/>
                        })}
                        {arrayEmpty.map((e)=>{
                            return <StarOutlineIcon key={e+"321"} style={{color:"#d47d8a"}}/>
                            })}
                    

                        </div>
                    })}

                </div>

            </div>
            <div className=' sub-card my-4'>
                <h4 className='title px-4'>{data.Aboutcontent.services.title}</h4>
                <div className='row mx-0'>
                    {data.Aboutcontent.services.content.length> 0 && data.Aboutcontent.services.content.map((e)=>{
                        return(<div key={e.name+"f,l"} className='col-sm-12 col-md-6 col-lg-6 card-sevice'>
                        <div className='part-div'>
                            <div className='py-4 text-center'>
                                {e.icon}
                            </div>
                            <div className='px-2 text-white text-center'>
                            {e.name}
                            </div>
                        </div>
                    
                    </div>)
                    })}
                   
                  
                    {/* <div className='col-sm-12 col-md-4 col-lg-4 card-sevice'>
                        <div className='part-div'>
                            <div className='py-4 text-center'>
                                <Filter3Icon style={{fontSize:"5vw"}}/>
                            </div>
                            <div className='px-2 text-white text-center'>
                                Lorem ipsum dolor sit amet consectetur adipisicing elit. Velit nobis dolorem aperiam inventore quisquam quas exercitationem esse, voluptates numqu
                            </div>
                        </div>
                        
                    </div> */}
                  
                </div>
                
            </div>


            </div>
     );
}

export default AboutContent;