import React from "react"
import toast, { Toaster } from 'react-hot-toast';
import CancelIcon from '@mui/icons-material/Cancel';
import { connect } from "react-redux";
function TostifyHot({changeLangToAr,isSelected,lang}){
 
    const [isToastify,setIsToastify]=React.useState(false)
    
    React.useEffect(()=>{
        // setTimeout(()=>{
            if(lang === "EN"){
                setIsToastify(true)
            }
           
        // },5000)
    },[])
    React.useEffect(()=>{
            if(isSelected){
            setIsToastify(false)
            }   
    },[isSelected])
    const handleClick =()=>{
        setIsToastify(false)
        changeLangToAr()
        

    } 
   
    return(<>
        <div className="toastify-div" style={isToastify?{opacity:"1"}:{opacity:"0"}}>
            <div className=" absolute-div d-flex">
                <div className="w-50 h-100 " style={{background:"#bf7ea1"}}></div>
                <div className="w-50 h-100" style={{background:"rgb(50 49 49)"}}></div>
            </div>
            <div>Do you want to change language to Arabic ? 
            &nbsp;<span onClick={handleClick}>yes</span>
            <div className="position-absolute" style={{top:0,right:"1rem",zIndex: "900",cursor:"pointer"}}>
             <CancelIcon onClick={()=>{
                setIsToastify(false)
             }} style={{fontSize:"16px"}}/>
            </div>
            </div>
        </div>

    
    </>)
}
const mapStateToProps =(state)=>{
    return{
        lang:state.lang
    }
}
const mapDispatchToProps= (dispatch)=>{
    return{
        changeLangToAr: ()=>{dispatch({type:"CHANGE_LANG_TO_AR"})}
    }
}
export default connect(mapStateToProps,mapDispatchToProps)(TostifyHot)