import * as React from 'react';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Box from '@mui/material/Box';
import { useNavigate } from 'react-router-dom';
import { connect } from 'react-redux';
import {Link} from "react-router-dom"

 function ColorTabs({returnedSelected,changeValue,lang,isSelected}) {
    const navigate= useNavigate()
  const [value, setValue] = React.useState('one');
  const [isEn,setIsEn] = React.useState(true)
  const [color,setColor] = React.useState({
    one:"",
    two:"grey",
    three:"grey",
    four:"grey",
    five:"grey"
  })

  React.useEffect(()=>{
    setValue(changeValue)
    setColor({
        one:"grey",
        two:"grey",
        three:"grey",
        four:"grey",
        five:"grey",
        [changeValue]:""
    })
    
  },[changeValue,value])
  React.useEffect(()=>{
    if(lang=== "EN"){
      setIsEn(true)
      
    }else if(lang === "AR"){
      setIsEn(false)
    }
  },[lang])
  

  const handleChange = (event, newValue) => {
    returnedSelected(newValue)
    setValue(newValue);
    
  };

  return (
    <Box sx={{ width: '100%' }}>
      <Tabs
        value={value}
        onChange={handleChange}
        textColor="secondary"
        indicatorColor="secondary"
        aria-label="secondary tabs example"

        
      >
        <Tab style={{color:color.one}} value="one" label={isEn?"Home":"الصفحة الرئيسية"} />
        <Tab style={{color:color.two}} value="two" label={isEn?"About":"حول"} />
        {/* <Tab style={{color:color.three}} value="three" label="Portifilo" /> */}
        <Tab style={{color:color.four}} value="four" label={isEn?"Resume":"سيرة ذاتية"} />
        <Tab style={{color:color.five}} value="five" label={isEn?"Contact":"معلومات الاتصال"} />
        {
          isSelected?"":<Link to={"/download/cv"}>
          <Tab style={{color:color.five}} value="six" label={isEn?"download":"تنزيل"} />  
        </Link>
        }
        
      </Tabs>
    </Box>
  );
}
const mapStateToProps =(state)=>{
  return{
    lang:state.lang
  }
}
export default connect(mapStateToProps)(ColorTabs)