import { Placeholder } from 'rsuite';

function PlaceHolder({dir}){
    return(
        <>
        <div id='home-page' style={{direction:dir}}>
        
        <div className='w-100 h-100 row mx-0 ' >
            <div className='col-lg-6 col-md-12 col-sm-12 d-flex justify-content-center align-items-center '>
                <div className=' w-100 position-relative  d-flex justify-content-center '>
                    <div className='w-75 '>
                        <h1 className='name_title'>
                        <Placeholder.Paragraph style={{ marginTop: 30 }} />
                        
                        </h1>
                        <h4 className='spc_title'>
                            
                        <Placeholder.Paragraph style={{ marginTop: 30 }} />
                        </h4>
                        <div style={{color:"white!important"}}>
                        <Placeholder.Grid rows={1} columns={5} active />
                        </div>
                        <div className='social_div' >
                        <Placeholder.Paragraph style={{ marginTop: 30,color:"pink" }} />
                        </div>
                    </div>
                </div>
             
            </div>
            <div className=' col-lg-6 col-md-12 col-sm-12 d-flex justify-content-center align-items-center icon_photo '>
            {/* <Placeholder.Paragraph style={{ marginTop: 30 }} rows={5} graph="image" active /> */}
            <Placeholder.Paragraph style={{ marginTop: 60 }} graph="circle" />
            </div>
        </div>
    </div>
    

  </>
    )
}
export default PlaceHolder;