import React from 'react';
import NavBar from '../component/Nav';
import imgBg from "../assets/img/bg.jpg"

function AboutePage() {
    const LANG = {
        "DE": {
            hello: 'Hallo'
         },
         "EN": {
            hello: 'Hello'
         },
         "FA": {
         hello: "سلام"
         }
      }
      
      
      const [currnetLang ,setCurrnetLang] = React.useState("EN")
      
      
      const setLang = lang => {
        setCurrnetLang(lang)
      }
    return ( <>
    <div className='mb-5' onClick={()=>{
        alert("jo")
        
        console.log(LANG[currnetLang]);
    }}>
        test
    </div>
    <button type="button" >Toggle Lang</button>
    <button type="button" onClick={()=>{setLang("EN")}} name="">EN</button>
    <button type="button" onClick={()=>{setLang("FA")}} name="">FA</button>
    <button type="button" onClick={()=>{setLang("DE")}} name="">DE</button>
        {/* <p>Current Language is {LANG[currnetLang]}</p> */}
        <p>{LANG[currnetLang].hello} username </p>
    {/* <NavBar/>
    <div style={{background:"red",height:"30rem"}}>hbyugjy</div> */}
    {/* <img src={imgBg} style={sty.imgBackground} /> */}
    </> );
}

export default AboutePage;
const sty={
    imgBackground:{
        width:"100%",
        height:"90vh",
        objectFit:"cover",
        filter:"brightness(0.5)"
    }
}