import React from 'react';
import ColorTabs from "../component/Tabs"
import {useTransition,animated} from "@react-spring/web"

function ContentPagesTemplate({isSelected,id,children,returnedValueSelect,changeValue,tempChange}) {
    const [isSelectedValue,setIsSelectedValue]=React.useState(false)
    const transition= useTransition(isSelected,{
        from:{opacity:0},
        enter:{opacity:1},
        leave:{opacity:0}
    })

    React.useEffect(()=>{
        setIsSelectedValue(isSelected)
        // alert(isSelected)
    })

    return ( 
        <>
        <div className='div-back'>

        </div>
        <div id={id} style={changeValue!=="one"?{zIndex:"1000",color:"white",opacity:"1"}:{zIndex:"-1",opacity:"0"}}
             className={tempChange===1?"animation-append":(tempChange===3?"animation-not-append":"")} >
            <div>
            <ColorTabs isSelected={isSelected} changeValue={changeValue} returnedSelected={(res)=>{
                                returnedValueSelect(res)
                                setIsSelectedValue(!setIsSelectedValue)
                                
                        }} />
            </div>
            {
                transition((style,item)=>item ?<animated.div style={style} > {children}</animated.div>:"")
            }
            
        </div>
        </>
     );
}

export default ContentPagesTemplate;